.card {
  display: flex;
  font-family: 'Roboto';
  font-weight: 500;
  font-size: 12px;
  line-height: 1.34;
  flex-direction: column;
  width: 280px;
  margin: auto;
  padding: 48px 20px 41px 20px;

  &__image {
    width: 240px;
    height: 357px;
    padding-bottom: 30px;
  }
  &__title {
    font-size: 20px;
    text-transform: uppercase;
    padding-bottom: 20px;
    line-height: 1.15;
  }

  &__info {
    display: flex;
    flex-direction: column;
    gap: 10px;
    padding-bottom: 20px;
    padding-left: 0px;
    margin: 0px;
  }
  &__info-details {
    display: flex;
    flex-direction: row;
    gap: 46px;
  }
  &__info-rate {
    display: flex;
  }
  &__info-popularity {
    display: flex;
    flex-direction: row;
    gap: 59px;
  }
  &__info-title {
    display: flex;
    flex-direction: row;
    gap: 47px;
  }
  &__info-gene {
    display: flex;
    flex-direction: row;
    gap: 80px;
  }
  &__about-title {
    text-transform: uppercase;
  }
  &__about-text {
    text-transform: none;
    padding-top: 10px;
    padding-bottom: 21px;
    margin: 0px;
    line-height: 1.67;
  }
  &__buttons {
    display: flex;
    flex-direction: row;
    gap: 15px;
  }
}
.rate {
  text-align: center;
  color: #ffffff;
  background: #ff6b01;
  border-radius: 5px;
  padding: 0px 10px;
  line-height: 1.17;
  &__total {
    padding: 0px 4px;
    line-height: 1.17;
    border-radius: 5px;
    background: #f7f7f7;
  }
}
.watched-btn {
  padding: 0px;
  width: 113px;
  height: 44px;
  text-transform: uppercase;
  background: #ff6b01;
  color: #ffffff;
  border-radius: 5px;
  border: 0px;
}
.queue-btn {
  background: none;
  padding: 0px;
  width: 113px;
  height: 44px;
  text-transform: uppercase;
  border: 1px solid #000000;
  border-radius: 5px;
}

@media screen and (min-width: 768px) and (max-width: 1279px) {
  .card {
    width: 618px;
    flex-direction: row;
    padding: 42px 30px 40px 28px;

    &__image {
      width: 264px;
      height: 374px;
      padding: 0px;
      padding-right: 32px;
    }

    &__about-title {
      max-width: 264px;
    }
  }
}

@media screen and (min-width: 1280px) {
  .card {
    width: 822px;
    padding: 50px 30px 40px 30px;
    flex-direction: row;
    &__title {
      font-size: 30px;
      line-height: 1.17;
    }
    &__image {
      width: 396px;
      height: 478px;
      padding: 0px;
      padding-right: 30px;
    }
    &__about-title {
      max-width: 396px;
    }
  }
}
